import React, { Component, PropTypes } from 'react'
import '../styles/RoomSelector.css'

class RoomSelector extends Component {
    static propTypes = {
        setRoom: PropTypes.func,
        roomName: PropTypes.string
    }

    handleClick = (e, roomObj) => {
        this.props.setRoom(roomObj)
    }

    render() {
        const { props, handleClick } = this

        return (
            <div>
                <h4>Room selector</h4>

                <p>You are in the '{props.roomName}'.</p>
                <hr />

                <ul>
                    <li onClick={ () => handleClick(this, {
                        id: '1',
                        hasRestrictions: false,
                        name: 'Main Room'
                    }) }>
                        <a className="waves-effect waves-light btn m-xs-b-1">
                            Main Room
                        </a>
                    </li>
                    <li onClick={ () => handleClick(this, {
                        id: '2',
                        hasRestrictions: true,
                        restrictions: {
                            minimumAge: 18
                        },
                        name: 'Over 18s Room'
                    }) }>
                        <a className="waves-effect waves-light btn m-xs-b-1">
                            Over 18s Room
                        </a>
                    </li>
                </ul>
            </div>
        )
    }
}

export default RoomSelector


