import React, { Component, PropTypes } from 'react'
import { connect } from 'react-redux'
import { setRoom } from '../actions'

import RoomSelector from '../components/RoomSelector'
import Room from './Room'

function mapStateToProps(state) {
    return {
        profile: state.profile.get('baseProfile'),
        userId: state.profile.get('userId'),
        roomId: state.dashboard.get('roomId'),
        roomName: state.dashboard.get('roomName')
    }
}

function mapDispatchToProps(dispatch) {
    return {
        setRoom: (roomId, roomName) => dispatch(setRoom(roomId, roomName))
    }
}

class Dashboard extends Component {
    static propTypes = {
        profile: PropTypes.object,
        userId: PropTypes.string,
        roomId: PropTypes.string,
        roomName: PropTypes.string,
        setRoom: PropTypes.func
    }

    setRoom = (roomObj) => {
        if (roomObj.hasRestrictions) {
            if (roomObj.restrictions.minimumAge) {
                if (this.getAge(this.props.profile.dateOfBirth) >= roomObj.restrictions.minimumAge) {
                    this.props.setRoom(roomObj.id, roomObj.name)
                } else {
                    console.log('You are not old enough to enter this room!')
                }
            }
        } else {
            this.props.setRoom(roomObj.id, roomObj.name)
        }
    }

    getAge = (dateOfBirth) => {
        const oneYear = 1000*60*60*24*365
        return Math.floor((Date.now() - new Date(dateOfBirth)) / oneYear)
    }

    render() {
        const { props, setRoom } = this
        return (
            <div>
                { !!props.profile.selfie && <div className="row m-xs-b-0">

                    <div className="col s3">
                        <RoomSelector setRoom={ setRoom } roomName={ this.props.roomName }/>
                    </div>

                    <div className="col s9">
                        <Room userId={ props.userId } userProfile={ props.profile } roomId={ props.roomId } />
                    </div>

                </div> }
            </div>
        )
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Dashboard)


