import { SET_ROOM } from '../constants'
import { fromJS } from 'immutable'

const INITIAL_STATE = fromJS({
    roomId: '1',
    roomName: 'Main Room'
})

export default function lobbyReducer(state = INITIAL_STATE, action = {}) {
    switch (action.type) {

        case SET_ROOM:
            let newState = state.update('roomId', () => action.payload.id)
            return newState.update('roomName', () => action.payload.name)

        default:
            return state
    }
}


